p:6px;grid-template-areas:"header indicator" "status indicator" "control control"}.content__header{grid-area:header;font-size:27px;font-weight:bold;margin:0}.content__status{grid-area:status;font-size:14px}.content__control{grid-area:control;margin-top:20px}.content__indicator{background:top right no-repeat;background-size:41px;color:#333;font-size:36px;font-weight:bold;grid-area:indicator;grid-column:2 / 2;grid-row:1 / 3;letter-spacing:0;padding-right:43px;text-align:right}.content__indicator_temprature-on{background-image:url(./images/icon_temperature_2@2x.png)}.content__indicator_light-off{background-image:url(./images/icon_sun@2x.png)}.modal-form{display:grid;grid-column:2;grid-column-gap:16px;grid-row-gap:20px;grid-template-columns:repeat(2, 1fr);grid-template-rows:1fr 60px;margin:20px}.modal-form__layout{align-items:center;background-color:rgba(51,51,51,0.5);display:none;grid-template-columns:1fr 630px 1fr;height:100%;left:0;overflow:auto;position:fixed;top:0;width:100%;z-index:1}.modal-form__content{background-color:#fff;border-radius:20px;padding:26px 25px}.modal-form__button{background-color:#fff;border-radius:20px;box-sizing:border-box;color:#333;font-size:18px;font-weight:bold;padding:20px;text-align:center;letter-spacing:0;cursor:pointer}.modal-form__button_apply{background-color:#FFD93E;color:#000}.modal-form__button_apply:hover{background-color:#FFCD00}.slider{position:relative;border-radius:30px;height:60px;margin-top:33px}.slider_temprature{background-image:linear-gradient(90deg, #068FFF 2%, #FFD800 51%, #F76B1C 100%)}.slider_light{background-image:linear-gradient(95deg, #FFE682 2%, #FF9E00 98%)}.slider__circle{position:fixed;height:60px;width:60px;border-radius:50%;border:4px #fff solid;margin-top:-4px;margin-left:339px;box-shadow:0 1px 10px -7px rgba(0,0,0,0.5);cursor:pointer}.slider__circle:before{border-radius:50%;content:" ";position:absolute;z-index:-1;top:0;left:0;right:0;bottom:0;border:3px solid rgba(33,33,33,0.1)}.slider__value{position:absolute;font-size:18px;color:#fff;letter-spacing:0;margin:20px;font-weight:bold}.slider__value_left{left:0}.slider__value_right{right:0}.slider__value_image{margin:calc((60px - 36px) / 2);margin-right:15px}.thermostat{position:relative;display:grid;grid-template-columns:1fr;grid-template-rows:221px;align-items:center;justify-items:center}.thermostat__active-circle{position:absolute;background:url(./images/Oval@1x.svg) no-repeat center center;height:100%;width:100%;clip-path:polygon(100% 0, 100% 80%, 50% 50%, 37.5% 100%, 0 100%, 0 0)}.thermostat__inactive-circle{opacity:.7;position:relative;background:url(./images/Oval_Inactive.svg) no-repeat center center;height:100%;width:100%;z-index:0;clip-path:polygon(50% 50%, 63% 100%, 100% 80%)}.thermostat__inner-circle{position:absolute;border-radius:50%;height:174px;width:178px;background:url(./images/background@1x.svg) no-repeat center center;box-shadow:0 2px 4px 0 rgba(134,121,71,0.45);display:grid;grid-template-columns:1fr;align-items:center;justify-items:center}.thermostat__text{font-size:66px;color:#333;letter-spacing:0;text-align:left;font-weight:bold}.thermostat__arrow{position:absolute;border-left:7px solid transparent;border-right:7px solid transparent;border-bottom:7px solid #333;border-radius:5px;transform:translateX(82px) translateY(21px) rotate(110deg)}
